
import Vue from "vue"
import SvgIcon from "@/components/SvgIcon.vue";

export default Vue.extend({
  name: "Note",
  components: {SvgIcon},

  data() {
    return {
      note: ''
    }
  },
  methods: {
    sendNote(note: string) {
      this.$emit('getNote', note)
    },
    clear() {
      this.note = ''
    }
  }
})
